import * as React from 'react'
import Link from 'gatsby-link'
import { Grid, Row, Col } from 'react-flexbox-grid';

const PageRowComponent = (props: any) => {
    return (
        <Grid fluid>
        <Row>
          <Col xs={6} md={3}>
            {props.children}
          </Col>
        </Row>
      </Grid>
    )
}

export default PageRowComponent



